
import Toast from 'vux/src/components/toast/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import {SubmitSuggestUrl} from '@/api/api'
export default{
data(){
return{
ToastShow:false,
ToastTest:'',
PopupShow:true,
UserInfo:{
name:'',
tel:'',
content:'',
origin:'请选择'
},
SelectList:[
{name:'请选择'},
{name:'南湖公安'},
{name:'秀洲公安'},
{name:'嘉善公安'},
{name:'平湖公安'},
{name:'海盐公安'},
{name:'海宁公安'},
{name:'桐乡公安'},
{name:'经开公安'},
{name:'港区公安'},
{name:'治安支队'},
{name:'交警支队'},
{name:'出入境管理局'}
]
}
},
watch:{
['UserInfo.origin'](){
console.log(this.UserInfo.origin)
}
},
methods:{
CheckInfo(){
if (this.UserInfo.name==="") {
this.ToastTest='请输入姓名'
this.ToastShow=true
return false
}
if (!this.checkMobile(this.UserInfo.tel)) {
this.ToastTest='请输入正确的电话号码'
this.ToastShow=true
return false
}
if (this.UserInfo.content==="") {
this.ToastTest='请输入建议'
this.ToastShow=true
return false
}
if (this.UserInfo.origin==="请选择") {
this.ToastTest='请选择问题反馈所属'
this.ToastShow=true
return false
}
return true
},
checkMobile(str){

var
re = /^1\d{10}$/
if (re.test(str)) {
return true
} else {
return false
}
},
SubmitAjax(){
this.$http.post(SubmitSuggestUrl,this.UserInfo).then((res)=>{
console.log(res.data)
})
},
Submit(){
if (this.CheckInfo()) {
this.SubmitAjax()
}

},
ClosePop(){
this.PopupShow=false
}
},
components:{
Toast,
Popup
}
}



